.footer__section {
	background-color: var(--first-color);
	padding: 2rem 0 3rem;
	display: grid;
}
.footer__section .container {
	row-gap: 3.5rem;
}
.footer__title {
	font-size: var(--h1-font-size);
	color: #fff;
	margin-bottom: var(--mb-0-25);
}
.footer__subtitle {
	color: #fff;
	font-size: var(--small-font-size);
}
.footer__links {
	display: flex;
	flex-direction: column;
	row-gap: 1.5rem;
	margin-top: 3rem;
}
.footer__link {
	color: #fff;
}
.footer__link:hover {
	color: var(--first-color-lighter);
}
