.skill__item {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	row-gap: 2rem;
}

.technology {
	text-align: center;
}

.icon {
	width: 3rem;
}
.subtitle__skll {
	color: var(--text-color-light);
}
.skill__type {
	display: inline-flex;
	align-items: center;
	margin-bottom: var(--mb-2-5);
}
.skill__type_last {
	margin-bottom: 0;
}
.icon__type {
	width: 2rem;
	margin: 0.5rem 0.75rem 0 0;
}
.icon__angle {
	width: 2rem;
}
.angle__button {
	background-color: var(--container-color);
	border: none;
	cursor: pointer;
}
.icon__close {
	width: 1.5rem;
}
.active {
	display: none;
	transition: 13s;
}
.toggle {
	margin-left: var(--mb-3);
}
